

.header {
    display: flex;
    justify-content: space-between;
    border: 1px solid black;
}

.logo {
    width: 200px;
}


.nav-items > ul {
    display: flex;
    font-size: 24px;
    list-style-type: none;
}

.nav-items > ul > li {
    padding: 10px;
    margin: 10px;
}

.res-container {
    display: flex;
    flex-wrap: wrap;
}

.res-card {
    width: 200px;
    padding: 5px;
    margin: 5px;
}
 .res-card:hover {
    cursor: pointer;
    border: 1px solid black;
 }

.Search {
    padding: 10px;
}

.res-logo {
     width: 100%;
}

.filter-btn {
    margin: 10px;
    cursor: pointer;


}

.shimmer-container {
    display: flex;
    flex-wrap: wrap;
}

.shimmer-card {
    width: 200px;
    height: 400px;
    margin: 20px;
    background-color: #f0f0f0;
}
.login {
    padding: 0 15px;
    cursor: pointer;
    width: 100px;
}
.filter {
    display: flex;
}
.search {
    margin-top: 10px;
}